@font-face {
  font-family: 'NeutralFace-Bold';
  src: local('NeutralFace-Bold'),
    url('./components/fonts/NeutralFace-Bold.woff') format('woff'),
    url('./components/fonts/NeutralFace-Bold.woff2') format('woff2');
  font-display: swap;
}

@font-face {
  font-family: 'NeutralFace';
  src: local('NeutralFace'),
    url('./components/fonts/NeutralFace.woff') format('woff'),
    url('./components/fonts/NeutralFace.woff2') format('woff2');
  font-display: swap;
}

/* Home Page */
body {
  background-color: #F2EFE4 !important;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /* border: 3px red solid; */
}

main {
  display: flex !important;
  justify-content: space-around !important;
  padding: 0px 100px 0px 100px !important;
  margin-top: 30px;
  /* border: red solid 3px; */
  max-height: 450px !important;
}

section {
  max-height: 270px;
  margin-top: 50px;
  /* border: yellow solid 3px; */
}

.bio {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

#title-header {
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 900 !important;
  font-style: normal !important;
  font-size: 110px !important;
  line-height: 125px !important;
  margin: 25px 0 0 25px !important;
  mix-blend-mode: exclusion;
  color: #F2EFE4 !important;
  font-display: swap;
}

img {
  width: 450px;
  border-radius: 300px 300px 50px 50px;
  outline: #000000 10px solid !important;
  border: #ffffff 5px solid !important;
}

h2 {
  font-family: neue-haas-grotesk-display, sans-serif !important;
  font-weight: 900 !important;
  font-style: normal;
}

h2 {
  font-size: 35px !important;
}

h3 {
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 500 !important;
  font-style: normal;
  color: #F2EFE4 !important;
  mix-blend-mode: exclusion;
  font-display: swap;
}

h4 {
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 500 !important;
  font-style: normal;
  color: #F2EFE4 !important;
  mix-blend-mode: exclusion;
  font-display: swap;
}

p {
  font-family: neue-haas-grotesk-display, sans-serif !important;
  font-weight: 500 !important;
  font-style: normal;
  font-size: 20px !important;
  font-display: swap;
}

/* MenuDeck, MenuItems */
.drawer {
  background-color: #755B83;

}

.drawer-body {
  margin: 0px !important;
}

.menu {
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 900 !important;
  font-style: normal !important;
  font-size: 110px !important;
  color: #F2EFE4 !important;
  font-display: swap;
}

.drawer-main {
  display: flex !important;
  flex-direction: row !important;
  justify-content: center !important;
  align-items: center;
  padding: 0;
  /* border: red 3px solid; */
}

.menu-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /* border: #000000 solid 3px; */
}

.home-link {
  padding-right: 16px;
  padding-left: 16px;
  padding-top: 8px;
}

.home-link:hover {
  background: var(--chakra-colors-blackAlpha-50);
}

.chakra-accordion {
  display: flex !important;
  flex-direction: row;
  justify-content: space-between;
  width: 65em;
  flex-shrink: 0;
  /* border: red 3px solid; */
}

.css-1d0ox2v {
  /* border: red 3px solid; */
  color: #F2EFE4;
  padding: 0px !important;
  background-color: transparent !important;
}

.menu-links {
  display: flex !important;
  flex-direction: column !important;
  align-items: flex-start !important;
}

.css-17mg6aq {
  border: none;
}

.menu-section {
  display: flex;
  justify-content: space-around;
  width: 75em !important;
  margin-top: 100px;
}

.folio {
  position: relative;
  min-width: 134px;
  /* border: red solid 2px; */

}

.menu-item {
  display: flex !important;
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 500 !important;
  font-style: normal !important;
  font-size: 40px !important;
  color: #F2EFE4 !important;
  font-display: swap;
}



/* Buttons */

.btn {
  background-color: transparent !important;
  padding: 0px;
}

.closeBtn {
  margin-right: 30px;
  margin-top: 15px;
}

ul {
  display: flex;
  flex-direction: row;
  width: 100em;
  margin-top: 100px !important;
  justify-content: space-around;
  /* border: red solid 3px; */
}

li {
  display: flex;
  flex-direction: column;
}

.link {
  /* display: block; */
  font-family: neue-haas-grotesk-display, sans-serif !important;
  font-weight: 500 !important;
  font-size: 30px;
  color: #F2EFE4 !important;
  font-display: swap;
}

.btnContainer {
  display: flex;
  position: absolute;
  left: 2em;
  bottom: 24em;
  width: 100px;
  justify-content: space-between;
  /* border: red solid 3px; */
}

.btnContainer2 {
  display: flex;
  position: absolute;
  left: 6em;
  bottom: 24em;
  width: 100px;
  justify-content: space-between;
}

.css-68beff,
.css-8xi4id,
.chakra-button {
  display: flex;
  position: static !important;
  color: #F2EFE4 !important;
  mix-blend-mode: exclusion !important;
  cursor: pointer;
  top: 0em;
  right: 0em;
}

.css-gighr:hover {
  background-color: transparent !important;
}

/* FOLIO */
.background {
  background-repeat: no-repeat !important;
  background-attachment: fixed !important;
  background-size: cover !important;
}

.dndy {
  background-color: #000000;
  background-image: url('./components/images/DNDYlogo.png') !important;
}

.daytrip {
  background-color: #25ABC5;
  background-image: url('./components/images/daytripper-logo.png') !important;
}

.thyme {
  background-color: #243119;
  background-image: url('./components/images/thyme.png') !important;
}

.emptrk {
  background-color: #C73D8D;
  background-image: url('./components/images/emp-trk2.png') !important;
}

.jate {
  background-color: #49ADFA;
  background-image: url('./components/images/JATE-2.png') !important;
}

.readme {
  background-color: #7AB75B;
  background-image: url('./components/images/README-2.png') !important;
}



.folio-head {
  font-family: 'NeutralFace-Bold', sans-serif !important;
  font-weight: 900 !important;
  font-style: normal !important;
  font-size: 110px !important;
  color: #F2EFE4 !important;
  mix-blend-mode: exclusion;
  font-display: swap;
}

.para {
  font-size: 18px !important;
}

.dndy-para,
.daytrip-para,
.thyme-para,
.emptrk-para,
.jate-para,
.readme-para {
  position: absolute;
  color: #ffffff !important;
  width: 325px;
  right: 60em;
  top: 20em;
}

.project-links {
  position: absolute;
  left: 75em;
  top: 30em;
}

.repo-link {
  font-family: aktiv-grotesk-extended, sans-serif !important;
  font-weight: 900 !important;
  font-style: normal !important;
  color: #ffffff !important;
  font-size: 32px;
  font-display: swap;
}

/* DEVELOPER */
.developer {
  background-color: #ffffff;
  border-radius: 25px !important;
}

/* Footer */
footer {
  display: flex;
  justify-content: center;
  background-color: transparent;
}

.vh {
  min-height: calc(100vh - 204px);
}